.introduction {
	width: 370px;
	.Card {
		width: 100%;
		background: #f7f8fc;
		border-radius: 24px;
		box-shadow: 0 1px 3px 0 rgba(158, 158, 167, 0.7);
		position: relative;
		.card-header {
			display: flex;
			overflow: hidden;
			background: #51449e;
			border-radius: 24px 24px 0 0;
			padding: 17px 18px 15px;
			color: #fff;
			.avatar-box {
				width: 64px;
				height: 64px;
				border-radius: 22px;
				overflow: hidden;
				background: #000;
				cursor: pointer;
				.avatar {
					width: 64px;
					height: 64px;
				}
			}
			.base-info-box {
				margin-left: 12px;
				font-size: 20px;
				font-family: Roboto-Black, Roboto;
				font-weight: 900;
				color: #fff;
				white-space: pre-line;
			}
			.showPosition {
				font-size: 14px;
                margin-top: 8px;
			}
		}
		.expert-content {
            padding: 14px 18px 24px 18px;
            font-size: 14px;
		}
	}
}
.Card-box {
    margin-top: 12px;
	padding: 8px 12px;
	position: relative;
    .card-content{
        font-size: 12px;
        color: #373737;
        .co-worker-message-item{
            font-size: 14px;
            line-height: 24px;
            color: #51449e;
        }
        .institution-message-item{
            font-size: 14px;
            line-height: 24px;
        }
    }
}
