.user-info {
	display: flex;
	margin-top: 24px;
	padding: 24px 160px;
    .card-title {
		font-size: 16px;
		color: #373737;
		font-weight: 700;
		margin-bottom: 12px;
		display: flex;
		justify-content: space-between;
		border-bottom: 2px solid #9da8eb;
        .title-text {
            display: inline-block;
            height: 28px;
            color: #4759c5;
            position: relative;
            display: flex;
            align-items: center;
        }
	}
    .chart{
        margin-top: 50px;
    }
}
