.list {
	flex: 1;
	padding: 0 48px;
	margin-right: 24px;
	.line {
		font-size: 12px;
		text-indent: 18px;
	}
	.search-row {
		display: flex;
	}
	.list-item {
		border-bottom: 1px solid #d5d5d5;
		border-radius: 2px;
		padding: 20px 0 14px;
		.content {
			position: relative;
			top: -2px;
			flex-grow: 1;
			width: calc(100% - 49px);
			line-height: 18px;
			.title-line {
				font-size: 16px;
				font-weight: 600;
			}
		}
		.tag-link {
			color: #067c08;
			font-weight: 400;
			font-size: 11px;
			line-height: 13px;
			cursor: pointer;
		}
		.conf-info-zone {
			color: #444;
			font-size: 13px;
			line-height: 28px;
		}
		.cited {
			margin-right: 18px;
			margin-left: 18px;
			span {
				font-size: 12px;
				font-weight: 400;
				color: #000;
				line-height: 18px;
				& + span {
					color: #0b7f0d !important;
					margin-left: 8px;
				}
			}
		}
	}
}
